/*-------------------------------Size bar and image grid-----------------------------*/
#parent {
  width: 100%;
  height: 100%;
  display: table;
}

/*-------------------------------Left side bar-----------------------------*/

/*Intiative*/
:root {
  --white: #fff;
  --yellow: #feb14b;
  --grey: #313638;
}

.initiatives {
  text-align: center;
  margin-top: 50px;
}

.initiatives * {
  display: block;
}

.initiatives label {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  font-size: 12pt;
  color: #313638;
  padding: 10px;
  width: 50%;
  margin: 0 auto;
  line-height: normal;
  cursor: pointer;
  transition: all 0.1s;
}

label[htmlFor="2020"] {
  font-size: 32pt;
  color: #feb14b;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: normal;
  cursor: pointer;
  transition: all 0.1s;
}

input[type="radio"] {
  display: none;
}

.initiatives label:hover {
  background: var(--yellow);
  color: var(--white);
}

input[type="radio"][id="2020"]:checked + label {
  background: var(--yellow);
  color: var(--white);
}

input[type="radio"][id="crush"]:checked + label {
  background: var(--grey);
  color: var(--white);
}

input[type="radio"][id="crush"]:checked ~ .grid .mondaymot,
input[type="radio"][id="crush"]:checked ~ .grid .memes,
input[type="radio"][id="crush"]:checked ~ .grid .mascot,
input[type="radio"][id="crush"]:checked ~ .grid .careers,
input[type="radio"][id="crush"]:checked ~ .grid .other {
  display: none;
}

input[type="radio"][id="memes"]:checked + label {
  background: var(--grey);
  color: var(--white);
}

input[type="radio"][id="memes"]:checked ~ .grid .crush,
input[type="radio"][id="memes"]:checked ~ .grid .mondaymot,
input[type="radio"][id="memes"]:checked ~ .grid .mascot,
input[type="radio"][id="memes"]:checked ~ .grid .careers,
input[type="radio"][id="memes"]:checked ~ .grid .other {
  display: none;
}

input[type="radio"][id="mondaymot"]:checked + label {
  background: var(--grey);
  color: var(--white);
}

input[type="radio"][id="mondaymot"]:checked ~ .grid .crush,
input[type="radio"][id="mondaymot"]:checked ~ .grid .memes,
input[type="radio"][id="mondaymot"]:checked ~ .grid .mascot,
input[type="radio"][id="mondaymot"]:checked ~ .grid .careers,
input[type="radio"][id="mondaymot"]:checked ~ .grid .other {
  display: none;
}

input[type="radio"][id="mascot"]:checked + label {
  background: var(--grey);
  color: var(--white);
}

input[type="radio"][id="mascot"]:checked ~ .grid .crush,
input[type="radio"][id="mascot"]:checked ~ .grid .memes,
input[type="radio"][id="mascot"]:checked ~ .grid .mondaymot,
input[type="radio"][id="mascot"]:checked ~ .grid .careers,
input[type="radio"][id="mascot"]:checked ~ .grid .other {
  display: none;
}

input[type="radio"][id="careers"]:checked + label {
  background: var(--grey);
  color: var(--white);
}

input[type="radio"][id="careers"]:checked ~ .grid .crush,
input[type="radio"][id="careers"]:checked ~ .grid .memes,
input[type="radio"][id="careers"]:checked ~ .grid .mondaymot,
input[type="radio"][id="careers"]:checked ~ .grid .mascot,
input[type="radio"][id="careers"]:checked ~ .grid .other {
  display: none;
}

input[type="radio"][id="other"]:checked + label {
  background: var(--grey);
  color: var(--white);
}
input[type="radio"][id="other"]:checked ~ .grid .crush,
input[type="radio"][id="other"]:checked ~ .grid .memes,
input[type="radio"][id="other"]:checked ~ .grid .mondaymot,
input[type="radio"][id="other"]:checked ~ .grid .mascot,
input[type="radio"][id="other"]:checked ~ .grid .careers {
  display: none;
}

/*-------------------------------Image grid gallery-----------------------------*/
ol {
  padding: 0px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  align-content: stretch;
  justify-content: center;
  width: 100%;
  padding: 30px 10px 0px 10px;
}

.grid li {
  padding: 12px;
}

a.darken {
  display: inline-block;
  position: relative;
  width: 100%;
  background: black;
  text-align: center;
  width: 21.9vw;
  height: 21.9vw;
  transition: all 0.3s linear;
}

a.darken img {
  transition: all 0.3s linear;
  position: absolute;
  left: 50%;
  top: 50%;
  height: auto;
  width: 22vw;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

a.darken:hover img {
  opacity: 0.2;
}

a.darken div {
  font-family: "Montserrat";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgba(255, 255, 255, 0);
}

a.darken:hover div {
  color: rgba(255, 255, 255, 1);
}

@media only screen and (max-width: 520px) {
  .side {
    font-size: 16pt;
  }

  a.darken {
    width: 24.8vw;
    height: 24.8vw;
  }

  a.darken img {
    width: 25vw;
  }
}

@media only screen and (max-width: 450px) {
  .initiatives label {
    width: 80%;
  }
}
@media only screen and (max-width: 300px) {
  a.darken {
    width: 36.9vw;
    height: 36.9vw;
  }

  a.darken img {
    width: 37vw;
  }

  .initiatives label {
    width: 100%;
  }
}
