/*-------------------------------Blog post Preview-----------------------------*/
/*Preview Image and Hover*/

.blog-preview {
  padding: 30px;
}

.preview-pic {
  width: 35.807vw;
  height: 22.786vw;
  transition: transform 0.5s ease;
  overflow: hidden;
}

.blog-post:hover .preview-pic img {
  -ms-transform: scale(1.05); /* IE 9 */
  -webkit-transform: scale(1.05); /* Safari 3-8 */
  transform: scale(1.05);
}

/*Author circle crop*/
.author-pic {
  border-radius: 50%;
  height: 80px;
  overflow: hidden;
  width: 80px;
  margin-right: 1.5vw;
  margin-left: 0px;
}

/*Author image*/
.vivw_img {
  height: auto;
  transform: translate(-5%, -10%);
}

/*Post preview*/
.blog-details {
  margin-bottom: 10px;
}

.heading {
  color: #feb14b;
  letter-spacing: 0.1vw;
  font-size: 11pt;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  margin-bottom: 10px;
}

.auth {
  color: #feb14b;
  letter-spacing: 0.1vw;
  font-size: 10pt;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
}

.heading:hover {
  text-decoration: none;
}

.subheading {
  color: #707070;
  letter-spacing: 0.1vw;
  font-size: 9pt;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  padding: 0;
  margin-bottom: 10px;
}

.date {
  color: #313638;
  letter-spacing: 0.1vw;
  font-size: 7pt;
  font-family: "Montserrat", sans-serif;
  padding: 0;
  margin: 0;
}

table {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  width: 70vw;
  height: 100%;
}

#preview-row {
  display: flex;
  align-items: center;
}

.author-name {
  width: 200px;
}

.author-section {
  vertical-align: middle;
}

@media screen and (max-width: 1000px) {
  table {
    width: 90%;
  }

  .author-name {
    width: auto;
    padding-left: 5px;
  }

  .author-pic {
    width: 50px;
    height: 50px;
  }
}

@media screen and (max-width: 800px) {
  .blog-preview {
    padding: 20px;
  }

  tr {
    flex-wrap: wrap;
    justify-items: flex-start;
    align-items: flex-start;
    width: 100%;
  }

  table {
    width: 95%;
  }

  .preview-pic {
    width: 100%;
    height: auto;
  }

  .author-section {
    padding-left: 10px;
  }

  .blog-details .date {
    font-size: 12px;
  }
}

@media screen and (max-width: 300px) {
  .author-section {
    display: flex;
    align-items: center;
    padding-left: 0px;
    padding-bottom: 10px;
  }
}
