@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*font-family: 'Playfair Display', serif;
font-family: 'Montserrat', sans-serif;*/

/*----------------------------------Header------------------------------------*/

.coverPhoto {
  height: 100vh;
  overflow: hidden;
}

.cover_image {
  height: 100%;
  overflow: hidden;
  width: 100%;
  object-fit: cover;
  position: absolute;
}

/*website overlay*/
.coverPhoto .dark {
  width: 100%;
  background-color: #313638;
  opacity: 0.6;
  position: absolute;
  top: 0;
  height: 100vh;
  z-index: 1;
}

.coverPhoto .title {
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  z-index: 2;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.coverPhoto .title h1 {
  margin: 0px;
}

@media only screen and (max-width: 700px) {
  .coverPhoto,
  .coverPhoto .dark,
  .coverPhoto .title,
  .cover_image {
    height: 50vh;
  }

  .coverPhoto .title h1 {
    font-size: 3.5vh;
  }
}

/*----------------------------------General------------------------------------*/
h1 {
  color: #feb14b;
  font-family: "Playfair Display", serif;
  font-size: 48pt;
}

h2 {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 30pt;
  text-transform: uppercase;
  padding-bottom: 20px;
  text-align: center;
}

.header {
  padding-top: 50px;
  padding-bottom: 20px;
  text-align: center;
}

.subheader {
  font-family: "Montserrat", sans-serif;
  font-size: 13pt;
  color: grey;
}

p {
  font-family: "Montserrat", sans-serif;
  font-size: 10pt;
  margin-top: 0;
}

.body_bold {
  font-family: "Montserrat", sans-serif;
  font-size: 10pt;
  font-weight: bold;
}

.centre {
  text-align: center;
}

.button {
  font-family: "Montserrat", sans-serif;
  background-color: #e85f5c;
  border-radius: 60px;
  border-style: none;
  color: white;
  padding: 10px 45px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.button:hover {
  background-color: #feb14b;
  color: #ffffff;
}

/*-----------------------------------Navbar-----------------------------------*/
.bg-custom {
  background: #fff7ef;
  font-size: 10pt;
  padding: 0px;
}

.nav {
  height: 70px;
}

.navbar-nav {
  color: #313638;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  text-decoration: none;
}

.nav-item {
  float: right;
  color: #313638;
  padding: 20px 5px;
  text-decoration: none;
  letter-spacing: 1.5px;
}

.nav-item-btn {
  color: #313638;
  padding-top: 22px;
  padding-right: 10px;
  display: none;
}

.nav-item a {
  color: #313638;
  padding: 10px 15px;
  text-decoration: none;
}

.dropdown-menu {
  border-color: transparent;
  letter-spacing: 1.5px;
  font-size: 14px;
  background-color: #fff7ef;
  margin-top: 15px;
  border-radius: 0px;
}

.dropdown-item {
  padding: 5px 0px;
}

.dropdown-item:hover {
  background-color: #fff7ef;
}

.menuTitle {
  color: #313638;
  padding: 10px 15px;
}

.menuTitle:hover {
  color: #e85f5c;
}

.nav-item a:hover {
  color: #e85f5c;
  padding: 10px 15px;
  text-decoration: none;
}

@media screen and (max-width: 1000px) {
  .nav-item {
    display: none;
  }

  .nav-item-btn {
    display: block;
  }
}

/*-----------------------------------Menu-----------------------------------*/

.menu-container {
  position: fixed;
  width: 100%;
  background: #fff7ef;
  opacity: 1;
  color: #313638;
  z-index: 3;
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.menu-item {
  color: #313638;
  padding: 5px 35px;
  text-decoration: none;
  letter-spacing: 1.5px;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 16px;
}

.menu-box:hover {
  width: 92%;
  background-color: #fff0e1c1;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
}

.menu-box:hover .menu-item {
  color: #e85f5c;
}

.menu-bar {
  float: none;
}

.menu-list {
  padding-top: 4rem;
}

.menu-dropdown {
  padding: 5px 0px;
}

/*----------------------------------Footer------------------------------------*/
footer {
  margin: 0px;
  padding: 0px;
  background-color: #fff7ef;
  min-width: 100%;
}

.footer-all {
  list-style-type: none;
  margin: 1.4vw auto;
  padding: 4vw 0vw 2vw 0vw;
}

.footer-item {
  font-family: "Montserrat", sans-serif;
  padding: 0.33vw;
  color: #313638;
  text-transform: uppercase;
  font-size: 1.05vw;
}

.bottom-footer-item {
  font-family: "Montserrat", sans-serif;
  padding: 0.3vw;
  color: #313638;
  text-transform: uppercase;
}

.footer-logo {
  margin-top: 1%;
}

.footer-icons {
  padding-right: 1vw;
}

.col-width {
  flex-basis: 40%;
}

.socials {
  margin-left: 8%;
}

.footer-copyright {
  font-family: "Montserrat", sans-serif;
  text-transform: none;
  padding-bottom: 1vw;
  font-size: 1.05vw;
}

.footer-copyright,
a {
  color: #313638;
}

.footer-item a:hover {
  color: #feb14b;
  text-decoration: none;
}

.bottom-footer-item a:hover {
  color: #feb14b;
  text-decoration: none;
}

.grid-footer-container {
  display: flex;
  width: 70%;
  margin: 0px auto;
}

@media screen and (max-width: 600px) {
  .grid-footer-container {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .footer-item {
    text-align: center;
    font-size: 16px;
  }

  .footer-copyright {
    font-size: 16px;
  }

  .footer-logo {
    margin-bottom: 2%;
  }

  .footer-icons {
    margin-right: 2vw;
  }

  .socials {
    margin-left: 0%;
    margin-top: 1%;
  }

  .footer-all {
    margin: 1.5vw auto 1vw auto;
  }
}

/*-------------------------------Home page header-----------------------------*/
/*Header image*/
.contain {
  background-image: url("/header-1.png");
  background-position: center bottom 40%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  max-width: 100%;
  background-color: #313638;
}

/*Header title*/
div .headline {
  text-align: left;
  position: absolute;
  top: 30%;
  left: 54vw;
}

.headline h1 {
  color: #feb14b;
  font-family: "Playfair Display", serif;
  font-size: 5.5vw;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/*Header subtitle*/
.headline p {
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 1.17vw;
  letter-spacing: 2.5px;
}

/*-----------------------------Home page description--------------------------*/
/*Description wrapper*/
.description {
  height: 500px;
  margin: auto;
  max-width: 80%;
}

/*Description left block*/
.description-left {
  display: inline-block;
  float: left;
  height: 500px;
  padding: 100px 50px 50px 0px;
  width: 50%;
}

/*Left block text*/
.description-left h1 {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 78px;
  font-size: 6.4vw;

  font-weight: 600;
  letter-spacing: 4px;
  line-height: 100px;
  padding: 0px, 0px, 50px, 200px;
  text-align: right;
}

/*Description right block*/
.description-right {
  display: inline-block;
  float: right;
  height: 500px;
  line-height: 22px;
  padding: 175px 0px 0px 50px;
  width: 50%;
}

/*Right block text*/
.description-right p {
  color: #313638b3;
  font-family: "Montserrat", sans-serif;
  font-size: 11pt;
  text-align: justify;
}

/*--------------Responsive Mobile Design-------------------*/

@media only screen and (max-width: 850px) {
  .contain {
    background-position: center bottom 40%;
    height: 66vh;
  }

  /*Header title*/
  div .headline {
    position: absolute;
    top: 20%;
    left: 20vw;
  }
  .headline h1 {
    font-size: 8vw;
    text-transform: uppercase;
    letter-spacing: 3px;
  }
  /*Header subtitle*/
  .headline p {
    font-size: 1.6vw;
    letter-spacing: 2.5px;
  }

  .description-left {
    width: 100%;
    padding: 50px 0px 0px 0px;
    height: auto;
  }
  .description-left h1 {
    padding: 0px;
    font-size: 11vw;
    line-height: 90px;
  }
  .description-right {
    width: 100%;
    height: auto;
    padding: 20px 0px 40px 0px;
  }
}

/*---------------------------Home page statistics-----------------------------*/

/*Background image*/
.stats-background {
  width: 100%;
  height: 300px;
}

.stats {
  position: relative;
}

.number {
  display: inline-grid;
  grid-template-columns: 30% 30% 45%;
  left: 0;
  margin: 0 auto;
  place-items: center;
  position: absolute;
  right: 0;
  text-align: center;
  width: 60%;
  bottom: 25%;
}

.number span {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 75pt;
  font-weight: 500;
  letter-spacing: 15px;
}

.number p {
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 14pt;
}

/*--------------Statistics Mobile Design-------------------*/

@media only screen and (max-width: 1000px) {
  .number {
    grid-template-columns: 25% 30% 45%;
    position: absolute;
    right: 0;
    width: 90%;
    bottom: 25%;
  }

  .number span {
    letter-spacing: 15px;
  }
  .number p {
    font-size: 2vw;
  }
}

@media only screen and (max-width: 700px) {
  .number span {
    font-size: 14vw;
    letter-spacing: 10px;
  }
  .number p {
    font-size: 2.4vw;
  }
}

/*-------------------------------Home page events-----------------------------*/
/*Events block*/
.events {
  display: grid;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: 100px 110px auto auto;
  padding: 100px 0px 0px 0px;
  max-width: 80%;
  margin: 0 auto;
}

/*Events description grid area*/
.events-description {
  grid-area: 1 / 1 / 3 / 3;
}

/*Events description text*/
.events-description p {
  color: #313638b3;
  font-family: "Montserrat", sans-serif;
  font-size: 11pt;
  text-align: right;
}

.events-description p#about {
  padding: 20px;
  padding-bottom: 0;
}

.events-description a {
  font-family: "Montserrat", sans-serif;
  text-align: right;
  color: #e85f5c;
  padding-right: 20px;
}

.events-description a:hover {
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  color: #feb14b;
}

/*Events title grid area*/
.events-title {
  text-align: left;
  grid-area: 1 / 3 / 2 / 6;
  padding: 0 20px;
}

/*Big event image grid area*/
.event-1 {
  grid-area: 2 / 3 / 5 / 6;
  padding: 20px;
}

/*Big event image size*/
.event-1 img {
  object-fit: cover;
  width: 100%;
  transition: transform 0.5s ease;
  overflow: hidden;
}

.event-1:hover img { /* IE 9 */ /* Safari 3-8 */
  transform: scale(1.03);
}

/*Small event grid area*/
.event-2 {
  grid-area: 3 / 1 / 5 / 3;
  padding: 20px;
}

/*Small event image size*/
.event-2 img {
  object-fit: cover;
  width: 100%;
}

/*--------------Events Mobile Design-------------------*/

@media only screen and (max-width: 850px) {
  .events {
    padding: 20px 0px;
    max-width: 85%;
    margin: 0 auto;

    display: flex;
    flex-flow: column;
  }

  /* Use a Flexbox to switch positions of
Title and Description. */
  /*Events title - flexbox*/
  .events-title {
    text-align: center;
    order: 1;
  }

  /*Events description text - flexbox*/
  .events-description {
    order: 2;
  }

  /*Events description text*/
  .events-description p {
    text-align: justify;
  }
  .events-description a {
    text-align: justify;
    padding-left: 20px;
  }

  /*Big event image area - flexbox*/
  .event-1 {
    order: 3;
  }

  /* Currently, mobile dev ignores "small event" area.*/
}

/*------------------------------Home page sponsors----------------------------*/
/*Sponsors image position*/
.sponsors {
  text-align: center;
  margin: auto;
  padding: 0px 0px 30px 0px;
}

.sponsors h1 {
  padding-top: 90px;
  font-size: 60px;
  /*color: #feb14b;
  font-family: 'Playfair Display', serif;
  font-size: 5.5vw;
  text-transform: uppercase;
  letter-spacing: 3px;
*/

  font-family: "Montserrat", sans-serif;
  font-size: 5.5vw;
  font-size: 55px;

  font-weight: 500;
  letter-spacing: 3px;
  line-height: 100px;
  text-align: center;
}

.sponsors img {
  padding: 0px;
  width: 100%;
  max-width: 900px;
}

/*--------------Sponsors Mobile Design-------------------*/

@media only screen and (max-width: 640px) {
  .sponsors h1 {
    font-size: 7.8vw;
    padding-top: 30px;
  }

  .description {
    max-width: 85%;
  }

  .events-description p#about {
    padding-left: 0px;
    padding-right: 0px;
  }

  .event-1 {
    padding-left: 0px;
    padding-right: 0px;
  }

  .events-description a {
    padding: 0px;
  }
}

/*-------------------------------Blog post content-----------------------------*/
.blog-content {
  width: 100%;
  max-width: 950px;
  padding: 25px;
  text-align: center;
  margin: auto;
  margin-bottom: 6vw;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

/*Blog title*/
.blog-title {
  font-size: 34pt;
  letter-spacing: 4px;
  margin-top: 8vw;
}

/*Blog subtitle*/
.blog-subtitle {
  font-size: 12pt;
  color: #313638;
  text-align: center;
  margin-bottom: 2vw;
  font-style: italic;
}

/*Blog content*/
.post {
  margin-bottom: 8vw;
  margin: auto;
  color: #707070;
  font-family: "Montserrat", sans-serif;
  text-align: justify;
}

p,
th,
td {
  font-size: 12pt;
}

b {
  color: #000000;
  font-weight: bolder;
}

p.date {
  color: #313638;
  font-size: 10pt;
  text-align: center;
  margin-bottom: 4vw;
}

.post-content {
  font-weight: 300;
}

.post-heading {
  color: #707070;
  font-size: 24pt;
}

.question {
  font-weight: 500;
}

.list {
  color: #707070;
  font-family: "Montserrat", sans-serif;
  font-size: 12pt;
  text-align: justify;
  font-weight: 300;
}

.super-emph {
  font-size: 20pt;
  font-weight: bolder;
}

.pic {
  margin: 0 auto;
  max-width: 40%;
  margin-bottom: 4vw;
}

img.floatRight {
  float: right;
  padding: 30px;
}

.post-content a {
  text-decoration: underline;
}

.post-content a:hover {
  color: #feb14b;
}

.blog-content img {
  height: 300px;
  max-width: 100%;
  padding: 20px;
}

/* Use a .img-box div around image rows
to ensure centering. Example: blog-post-22*/
.blog-content .img-box {
  width: 100%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.outer-box {
  display: flex;
}

.inner-box {
  flex: 50% 1;
}

a:hover {
  color: #313638;
}

.img-box a {
  font-size: 11pt;
}

.post table {
  border-collapse: collapse;
  width: 100%;
}

.post th,
.post td {
  padding: 10px;
  text-align: left;
}

.post th {
  background-color: #feb14b;
  color: white;
  font-weight: 500;
}

/*Used for pictures in blog 34*/
.img-general {
  width: 35%;
  height: auto;
  object-fit: contain;
  margin: none;
  padding: none;
}

/*-------------------------------Author card-----------------------------*/
.author {
  margin: 0 auto;
  margin-bottom: 6vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*Author icon*/
.author-left {
  display: inline-block;
  width: 150px;
  height: 150px;
}

.author-left div {
  border-radius: 50%;
  height: 100%;
  overflow: hidden;
  width: 100%;
}

.vivw_img {
  height: auto;
  transform: translate(-5%, -10%);
  width: 120%;
}

.vic_img {
  height: auto;
  transform: translate(-5%, -5%);
  width: 120%;
}

/*Author details*/
.author-right {
  display: inline-block;
  padding: 0px 0px 0px 30px;
  letter-spacing: 2px;
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  text-align: left;
}

.author-name {
  font-family: "Playfair Display", serif;
  font-size: 20pt;
  margin: 0;
  padding: 0;
}

.position {
  margin-top: 5px;
  font-size: 14pt;
  color: #e85f5c;
}

.author-right p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #707070;
  font-size: 1vw;
  margin-top: 10px;
}

/*-------------------------------Blog mobile version-----------------------------*/
@media only screen and (max-width: 800px) {
  .videoWrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    padding-top: 25px;
    height: 0;
  }

  .videoWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .pic {
    max-width: 70%;
  }
}

@media only screen and (max-width: 700px) {
  .outer-box {
    flex-direction: column;
  }
}

@media only screen and (max-width: 600px) {
  .blog-title {
    font-size: 20pt;
  }

  .post-heading {
    font-size: 18pt;
  }

  .blog-subtitle {
    font-size: 12pt;
  }

  p {
    font-size: 12pt;
  }

  .author {
    flex-flow: column;
    width: 100%;
  }

  .author-right {
    text-align: center;
    padding: 0px;
  }

  .author-name {
    font-size: 16.5pt;
  }

  .position {
    font-size: 12pt;
  }

  .blog-content img {
    height: auto;
    padding: 5px;
  }
}

@media only screen and (max-width: 400px) {
  .blog-title {
    font-size: 16.5pt;
  }

  .post-heading {
    font-size: 16.5pt;
  }

  .pic {
    max-width: 100%;
  }

  img.floatRight {
    float: none;
  }

  .blog-content p:not(.date),
  .blog-content li {
    text-align: left;
  }
}

/*-------------------------------Join us socials------------------------------*/
